@import url(https://fonts.googleapis.com/css2?family=Heebo&display=swap);
* {
  box-sizing: border-box;
  margin: 0px;
  font-family: "Heebo", sans-serif;
}

.App {
    display: flex;
    flex-direction: column;
    width: 100%;
}


.Cell{
  width: 20px;
  height: 20px;
  border-top: 1px solid black;
  border-left: 1px solid black;
}

.Filled {
  background-image:url(/Graph-Algorithms-Visualization/static/media/start.23c992f7.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-animation-name: specialNodes;
          animation-name: specialNodes;
  -webkit-animation-duration: 2.0s;
          animation-duration: 2.0s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.Wall {
  -webkit-animation-name: wallAnimation;
          animation-name: wallAnimation;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.Target {
  background-image:url(/Graph-Algorithms-Visualization/static/media/target.f4350bfb.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-animation-name: specialNodes;
          animation-name: specialNodes;
  -webkit-animation-duration: 2.0s;
          animation-duration: 2.0s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.ShortestPath {
  background-color: lightgoldenrodyellow;
}

.Searched {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

.FinalSearched {
  background-color: lightseagreen;
}

@-webkit-keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(41, 41, 107, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(132, 150, 174, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(55, 162, 233, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: lightseagreen;
  }
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(41, 41, 107, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(132, 150, 174, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(55, 162, 233, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: lightseagreen;
  }
}

@-webkit-keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: lightgrey;
  }

  50% {
    transform: scale(1.2);
    background-color: grey;
  }

  100% {
    transform: scale(1);
    background-color: rgb(24, 21, 21);
  }
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.3);
    background-color: lightgrey;
  }

  50% {
    transform: scale(1.2);
    background-color: grey;
  }

  100% {
    transform: scale(1);
    background-color: rgb(24, 21, 21);
  }
}

@-webkit-keyframes specialNodes {
  0% {
    transform: scale(.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1.0);
  }
}

@keyframes specialNodes {
  0% {
    transform: scale(.3);
  }

  50% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1.0);
  }
}
